guest-invi-room-single>span[data-v-35a24cc2]:nth-child(6){padding-left:0!important;width:15%!important}.guest-invi-room-single>span[data-v-35a24cc2]:nth-child(6){text-align:right}.guest-acc-accept[data-v-35a24cc2]{position:relative;width:100%!important;z-index:99}.guest-acc-accept>.guest-invi-heading[data-v-35a24cc2]{text-align:center!important}.guest-invi-acc-cont>div[data-v-35a24cc2]{position:relative;z-index:999}.guest-invi-acc-cont>img[data-v-35a24cc2]:first-child{z-index:9}}@media screen and (max-width:576px){.guest-acc-room-container .guest-invi-heading[data-v-35a24cc2]{margin-bottom:30px!important;text-align:left!important}}", "",{"version":3,"sources":["webpack://./resources/js/components/guest/invitationDesigns/invitationDefault/InvitationAccommodation.vue"],"names":[],"mappings":"AAoXA,sCAGA,qBAAA,CAFA,iBAAA,CACA,iBAEA,CACA,0CAEA,WAAA,CADA,SAEA,CACA,sDAEA,QAAA,CAGA,YAAA,CAJA,iBAAA,CAEA,OAAA,CACA,WAEA,CACA,mCACA,YAAA,CACA,qBAAA,CACA,sBACA,CACA,qCAOA,UAAA,CANA,oBAAA,CAGA,mBAAA,CADA,eAAA,CAEA,gBAIA,CACA,8EARA,iBAAA,CAMA,kBAAA,CAFA,iBAaA,CATA,yCAMA,UAAA,CALA,mBAAA,CAGA,mBAAA,CADA,eAAA,CAEA,gBAIA,CACA,uCACA,aAAA,CACA,iBACA,CACA,8CAEA,WAAA,CAQA,UAAA,CANA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAPA,YAAA,CAEA,iBAAA,CAMA,iBAEA,CACA,0DACA,kBAAA,CACA,wBAAA,CACA,kBAAA,CACA,iBACA,CACA,2DACA,eAAA,CACA,wBAAA,CACA,kBAAA,CACA,aACA,CACA,iEAEA,kBAAA,CACA,wBAAA,CACA,kBAAA,CAHA,UAIA,CACA,kEACA,eAAA,CAEA,wBAAA,CACA,kBAAA,CAFA,aAGA,CACA,uCAIA,kBAAA,CAHA,YAAA,CACA,qBAAA,CACA,sBAEA,CACA,yCACA,SACA,CACA,sCACA,yBAAA,CACA,iBAAA,CAIA,eACA,CACA,2EAJA,YAAA,CACA,cAAA,CAFA,gBASA,CACA,uCAEA,kBAAA,CACA,iBAAA,CAEA,iBAAA,CAJA,gBAAA,CAGA,sBAAA,CAAA,iBAEA,CACA,uDAMA,UAAA,CALA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CACA,wDAMA,aAAA,CALA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CACA,4DACA,kBAAA,CACA,yBAAA,CACA,iBACA,CACA,gEACA,aAAA,CACA,iBACA,CACA,iFACA,oBAAA,CACA,iBACA,CACA,gFAIA,cAAA,CAHA,iBAAA,CACA,OAAA,CACA,OAEA,CACA,mCACA,YAAA,CACA,sBAAA,CACA,aACA,CACA,0CAQA,kBAAA,CACA,wBAAA,CACA,kBAAA,CAHA,UAAA,CAHA,cAAA,CACA,gBAAA,CAMA,iBAAA,CALA,iBAMA,CACA,+EAZA,mBAAA,CACA,iBAAA,CACA,eA0BA,CAhBA,qCAEA,8BAAA,CACA,uBAAA,CASA,UAAA,CAPA,YAAA,CAKA,mBAAA,CAJA,6BAAA,CAKA,gBAAA,CAPA,iBAAA,CAWA,iBAAA,CADA,OAAA,CAbA,oBAAA,CAYA,KAGA,CACA,wCAIA,kBAAA,CAHA,YAAA,CACA,qBAAA,CACA,sBAEA,CACA,4CAEA,WAAA,CADA,UAEA,CACA,+CAYA,qBAAA,CAVA,wBAAA,CACA,kBAAA,CAQA,aAAA,CANA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CARA,YAAA,CAGA,mBAAA,CAMA,iBAAA,CAGA,sBAAA,CAAA,iBACA,CACA,oHAEA,gBACA,CACA,sCACA,iBAAA,CACA,UACA,CACA,iDAYA,qBAAA,CAVA,wBAAA,CACA,kBAAA,CAQA,aAAA,CANA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAKA,gBAAA,CAbA,YAAA,CAGA,mBAAA,CAMA,iBAAA,CAGA,sBAAA,CAAA,iBAEA,CACA,gFAGA,oCAAA,CADA,YAEA,CACA,4CAMA,aAGA,CACA,0FAHA,aAAA,CANA,oBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAGA,YAaA,CAXA,8CAMA,UAAA,CAGA,oBAAA,CACA,qBACA,CACA,0DAEA,cAAA,CADA,eAAA,CAEA,gBACA,CACA,oCACA,uCACA,oBACA,CACA,yCACA,UACA,CACA,2CACA,oBACA,CAIA,uCACA,YACA,CACA,yCACA,eAAA,CACA,yBAAA,CACA,iBAAA,CAEA,cAAA,CACA,6BAAA,CAFA,iBAGA,CACA,0DACA,mBACA,CACA,2DAEA,gBAAA,CADA,mBAEA,CAIA,sHACA,mBACA,CAKA,sHAFA,wBAAA,CADA,mBAOA,CAJA,2DAEA,gBAEA,CAIA,mCAEA,iBAAA,CADA,oBAAA,CAEA,UACA,CACA,uDACA,2BACA,CAEA,0CACA,iBAAA,CACA,WACA,CACA,sDACA,SACA,CACA,CACA,oCACA,+DAEA,4BAAA,CADA,yBAEA,CACA","sourcesContent":["<template>\r\n    <div\r\n        class=\"guest-invi-acc-cont\"\r\n        v-if=\"\r\n            privileges.guest_invitation.accommodation != 0 &&\r\n            privileges.guest_invitation.accommodationStatus != 3\r\n        \"\r\n    >\r\n        <img src=\"/assets/guestInvi/acc-right.png\" alt=\"\" />\r\n        <div\r\n            v-if=\"privileges.guest_invitation.accommodationStatus == 1\"\r\n            class=\"guest-acc-accept\"\r\n        >\r\n            <h1 class=\"guest-invi-heading\">Accomodation</h1>\r\n            <h6 class=\"guest-invi-sub-heading\">\r\n                Accept Invitation to Accept accomodation\r\n            </h6>\r\n            <div>\r\n                <button\r\n                    @click=\"acceptAcc('accept')\"\r\n                    :class=\"{\r\n                        active: privileges.guest_invitation.inviteStatus == 6,\r\n                    }\"\r\n                    type=\"button\"\r\n                    :disabled=\"disableAcceptAcc\"\r\n                >\r\n                    Accept\r\n                </button>\r\n                <button\r\n                    @click=\"acceptAcc('decline')\"\r\n                    :class=\"{\r\n                        active: privileges.guest_invitation.inviteStatus == 6,\r\n                    }\"\r\n                    type=\"button\"\r\n                    :disabled=\"disableAcceptAcc\"\r\n                >\r\n                    Decline\r\n                </button>\r\n            </div>\r\n        </div>\r\n        <div\r\n            v-else-if=\"privileges.guest_invitation.accommodationStatus == 2\"\r\n            class=\"guest-acc-family-add\"\r\n        >\r\n            <div style=\"position: relative; width: 100%\">\r\n                <h1 class=\"guest-invi-heading\">Accomodation</h1>\r\n                <a\r\n                    class=\"guest-profile-link\"\r\n                    :href=\"route('guestprofile.index', user)\"\r\n                >\r\n                    <img\r\n                        v-if=\"screenWidth > 768\"\r\n                        style=\"margin-right: 10px\"\r\n                        src=\"/assets/guestInvi/Group-1689.svg\"\r\n                        alt=\"\"\r\n                    />\r\n                    <img\r\n                        v-else\r\n                        src=\"/assets/miscellenous/user-plus.svg\"\r\n                        alt=\"\"\r\n                    />\r\n                    <span v-if=\"screenWidth > 768\">Add Members</span>\r\n                </a>\r\n            </div>\r\n            <h6 class=\"guest-invi-sub-heading\">\r\n                Add Members Detatils to provide accomodation service\r\n            </h6>\r\n            <div class=\"guest-acc-fam-add-cont\">\r\n                <div class=\"guest-acc-fam-input\">\r\n                    <div v-if=\"hideUser == false\" class=\"guest-acc-fam-single\">\r\n                        <div>\r\n                            <span>{{ user.name }}</span>\r\n                            <img\r\n                                @click=\"hideUser = true\"\r\n                                src=\"/assets/guestInvi/close-fam-click.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                        </div>\r\n                        <div>Me</div>\r\n                    </div>\r\n                    <div\r\n                        v-for=\"item in selectedFam\"\r\n                        :key=\"item.id\"\r\n                        class=\"guest-acc-fam-single\"\r\n                    >\r\n                        <div>\r\n                            <span>{{ item.familyname }}</span>\r\n                            <img\r\n                                @click=\"addFamily(item, 'remove')\"\r\n                                src=\"/assets/guestInvi/close-fam-click.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                        </div>\r\n                        <div>{{ item.familyrelation }}</div>\r\n                    </div>\r\n                </div>\r\n                <div class=\"guest-acc-fam-list\">\r\n                    <div\r\n                        @click=\"addFamily(item, 'add')\"\r\n                        v-for=\"item in familyList\"\r\n                        :key=\"item.id\"\r\n                        class=\"guest-acc-fam-single\"\r\n                    >\r\n                        <div>{{ item.familyname }}</div>\r\n                        <div>{{ item.familyrelation }}</div>\r\n                    </div>\r\n                    <div\r\n                        @click=\"hideUser = false\"\r\n                        v-if=\"hideUser\"\r\n                        class=\"guest-acc-fam-single\"\r\n                    >\r\n                        <div>\r\n                            <span>{{ user.name }}</span>\r\n                        </div>\r\n                        <div>Me</div>\r\n                    </div>\r\n                </div>\r\n                <div class=\"guest-acc-submit\">\r\n                    <button\r\n                        :disabled=\"disableAccFamily\"\r\n                        @click=\"sendAccFamily()\"\r\n                        type=\"button\"\r\n                    >\r\n                        Send Details\r\n                    </button>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <div\r\n            v-else-if=\"\r\n                (privileges.guest_invitation.accommodationStatus == 4 ||\r\n                    privileges.guest_invitation.accommodationStatus == 5) &&\r\n                privileges.guest_invitation.hostAccommodationStatus == 0\r\n            \"\r\n            class=\"guest-acc-family-wait\"\r\n        >\r\n            <img src=\"/assets/guestInvi/acc-circle.png\" alt=\"\" />\r\n            <h1 class=\"guest-invi-heading\">Accomodation</h1>\r\n            <h6 class=\"guest-invi-sub-heading\">\r\n                Members Details has been sent. You will be notified when the\r\n                Accomodation details were allotted to you and your members\r\n            </h6>\r\n            <button @click=\"$emit('help')\" type=\"button\">\r\n                <img src=\"/assets/guestInvi/clarity_help-line.svg\" alt=\"\" />\r\n                <span>Help</span>\r\n            </button>\r\n        </div>\r\n        <div\r\n            v-else-if=\"\r\n                (privileges.guest_invitation.accommodationStatus == 4 ||\r\n                    privileges.guest_invitation.accommodationStatus == 5) &&\r\n                privileges.guest_invitation.hostAccommodationStatus == 1\r\n            \"\r\n            class=\"guest-acc-room-container\"\r\n        >\r\n            <div class=\"guest-acc-room-head\">\r\n                <h1 class=\"guest-invi-heading\">Accomodation</h1>\r\n                <div style=\"position: absolute; right: 0; top: 0\">\r\n                    <button>\r\n                        <!-- <a :href=\"route('guestacc.export', [user, inviStatus])\"\r\n                            ><img src=\"/assets/guestInvi/Group-1702.svg\" alt=\"\"\r\n                        /></a> -->\r\n                        <img src=\"/assets/guestInvi/Group-1702.svg\" alt=\"\" />\r\n                    </button>\r\n                    <button @click=\"$emit('help')\" type=\"button\">\r\n                        <img\r\n                            src=\"/assets/guestInvi/clarity_help-line.svg\"\r\n                            alt=\"\"\r\n                        />\r\n                        <span v-if=\"screenWidth > 768\">Help</span>\r\n                    </button>\r\n                </div>\r\n                <div class=\"guest-invi-room-all\">\r\n                    <div class=\"guest-invi-room-head\">\r\n                        <span style=\"width: 20%\">Name</span>\r\n                        <span style=\"width: 20%\">Phone Number</span>\r\n                        <span style=\"width: 20%\">Hotel</span>\r\n                        <span style=\"width: 20%\">Block</span>\r\n                        <span style=\"width: 10%\">Floor</span>\r\n                        <span style=\"width: 10%\">Room.No</span>\r\n                    </div>\r\n                    <div\r\n                        v-if=\"\r\n                            privileges.guest_invitation.accommodationStatus == 4\r\n                        \"\r\n                        class=\"guest-invi-room-single\"\r\n                    >\r\n                        <span style=\"width: 20%\">{{ user.name }}</span>\r\n                        <span style=\"width: 20%\">{{ user.mobile }}</span>\r\n                        <span style=\"width: 20%\">{{\r\n                            guestAccommodationInfo?.hotelName\r\n                        }}</span>\r\n                        <span style=\"width: 20%\">{{\r\n                            guestAccommodationInfo?.block\r\n                        }}</span>\r\n                        <span style=\"width: 10%\">{{\r\n                            guestAccommodationInfo?.floor\r\n                        }}</span>\r\n                        <span style=\"width: 10%\">{{\r\n                            guestAccommodationInfo?.roomno\r\n                        }}</span>\r\n                    </div>\r\n                    <div\r\n                        v-for=\"(item, index) in accInfo\"\r\n                        :key=\"index\"\r\n                        class=\"guest-invi-room-single\"\r\n                    >\r\n                        <span style=\"width: 20%\">{{\r\n                            item.family.familyname\r\n                        }}</span>\r\n                        <span style=\"width: 20%\">{{\r\n                            item.family.familymobile\r\n                        }}</span>\r\n                        <span style=\"width: 20%\">{{ item.hotelName }}</span>\r\n                        <span style=\"width: 20%\">{{ item.block }}</span>\r\n                        <span style=\"width: 10%\">{{ item.floor }}</span>\r\n                        <span style=\"width: 10%\">{{ item.roomno }}</span>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <flashMessage :message=\"message\"></flashMessage>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport flashMessage from \"../../../FlashMessage.vue\";\r\n\r\nexport default {\r\n    components: {\r\n        flashMessage,\r\n    },\r\n    props: [\r\n        \"inviStatus\",\r\n        \"familyAcc\",\r\n        \"user\",\r\n        \"accomodationInfo\",\r\n        \"guestAccommodationInfo\",\r\n    ],\r\n    data() {\r\n        return {\r\n            privileges: this.inviStatus,\r\n            familyList: [...this.familyAcc],\r\n            selectedFam: [],\r\n            disableAcceptAcc: false,\r\n            disableAccFamily: false,\r\n            hideUser: false,\r\n            accInfo: this.accomodationInfo,\r\n            message: null,\r\n            screenWidth: window.screen.width,\r\n        };\r\n    },\r\n    methods: {\r\n        addFamily(ele, type) {\r\n            if (type == \"add\") {\r\n                let index = this.familyList.findIndex((x) => x.id == ele.id);\r\n                this.familyList.splice(index, 1);\r\n                this.selectedFam.push(ele);\r\n            }\r\n            if (type == \"remove\") {\r\n                let index = this.selectedFam.findIndex((x) => x.id == ele.id);\r\n                this.selectedFam.splice(index, 1);\r\n                this.familyList.push(ele);\r\n            }\r\n        },\r\n        acceptAcc(type) {\r\n            let _this = this;\r\n            if (this.privileges.guest_invitation.inviteStatus != 6) {\r\n                return;\r\n            }\r\n            this.disableAcceptAcc = true;\r\n            let link = route(\"guestprivilege.update\", [\r\n                _this.user,\r\n                _this.privileges,\r\n            ]);\r\n            let acc = 2;\r\n            if (type == \"decline\") {\r\n                acc = 3;\r\n            }\r\n            axios({\r\n                method: \"POST\",\r\n                url: link,\r\n                data: { accommodation: acc },\r\n            })\r\n                .then((response) => {\r\n                    console.log(response);\r\n                    _this.disableAcceptAcc = false;\r\n                    _this.$emit(\"acc\", response.data);\r\n                    // _this.privileges = response.data;\r\n                    if (type == \"accept\") {\r\n                        _this.message = \"Accomodation Accepted!!\";\r\n                    }\r\n                    if (type == \"decline\") {\r\n             