n            //         item.classList.remove(\"active\");\r\n            //     }\r\n            // });\r\n            // conts.forEach(function (item) {\r\n            //     if (item.classList.contains(\"active\")) {\r\n            //         item.classList.remove(\"active\");\r\n            //     }\r\n            // });\r\n            // mainCont.css(\"display\", \"block\");\r\n            // currEle.classList.add(\"active\");\r\n            // currEle.nextElementSibling.classList.add(\"active\");\r\n        },\r\n        showAlbums(e) {\r\n            let all = document.getElementsByClassName(\"album-pics\");\r\n            let conts = document.getElementsByClassName(\"album-cont\");\r\n            let mainCont = $(\"#all-albums\");\r\n            all.forEach(function (item) {\r\n                if (item.classList.contains(\"active\")) {\r\n                    item.classList.remove(\"active\");\r\n                }\r\n            });\r\n            conts.forEach(function (item) {\r\n                if (item.classList.contains(\"active\")) {\r\n                    item.classList.remove(\"active\");\r\n                }\r\n            });\r\n            mainCont.css(\"display\", \"grid\");\r\n        },\r\n    },\r\n    computed: {\r\n        sixImages() {\r\n            let images = [];\r\n            let all = this.inviImages;\r\n            let index = 0;\r\n            if (all.length > 0) {\r\n                all.every((item) => {\r\n                    item[\"type\"] = \"img\";\r\n                    images.push(item);\r\n                    index++;\r\n                    if (index == 6) {\r\n                        return false;\r\n                    } else {\r\n                        return true;\r\n                    }\r\n                });\r\n            }\r\n            if (images.length < 6) {\r\n                let videos = this.inviVideos;\r\n                if (videos.length > 0) {\r\n                    videos.every((item) => {\r\n                        item[\"type\"] = \"vid\";\r\n                        images.push(item);\r\n                        index++;\r\n                        if (index == 6) {\r\n                            return false;\r\n                        } else {\r\n                            return true;\r\n                        }\r\n                    });\r\n                }\r\n            }\r\n            return images;\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.guest-invi-gallary-cont {\r\n    padding: 68px;\r\n    background: #ffffff;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n.guest-invi-heading {\r\n    font-family: \"Perpetua\";\r\n    font-style: normal;\r\n    font-weight: 700;\r\n    font-size: 29.5136px;\r\n    line-height: 34px;\r\n    text-align: center;\r\n    color: #333333;\r\n    margin-bottom: 15px;\r\n}\r\n.guest-invi-gallary {\r\n    display: grid;\r\n    grid-template-columns: repeat(3, 1fr);\r\n    grid-template-rows: max-content;\r\n    grid-column-gap: 10px;\r\n    grid-row-gap: 20px;\r\n    align-items: flex-start;\r\n    background: #f8edf0;\r\n    width: 55%;\r\n    padding: 19px 25px;\r\n    margin: auto;\r\n}\r\n.guest-invi-gallary > div {\r\n    position: relative;\r\n    padding-top: 85%;\r\n}\r\n.guest-invi-gallary > div > img,\r\n.guest-invi-gallary > div > video {\r\n    position: absolute;\r\n    top: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    object-fit: cover;\r\n}\r\n.guest-invi-gallary-cont > button:nth-child(3) {\r\n    outline: none;\r\n    border: 1px solid #d8839f;\r\n    border-radius: 10px;\r\n    background: white;\r\n    padding: 12px 32px;\r\n    width: max-content;\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    font-size: 13px;\r\n    line-height: 20px;\r\n    text-align: center;\r\n    color: #c4456f;\r\n    margin-top: 46px;\r\n}\r\n@media screen and (max-width: 1140px) {\r\n    .guest-invi-gallary {\r\n        width: 80% !important;\r\n        /* grid-template-columns: repeat(auto-fill, minmax(182px, 1fr)); */\r\n    }\r\n}\r\n@media screen and (max-width: 910px) {\r\n    .guest-invi-gallary {\r\n        width: 100% !important;\r\n    }\r\n}\r\n@media screen and (max-width: 576px) {\r\n    .guest-invi-gallary-cont {\r\n        padding: 68px 15px;\r\n    }\r\n    .guest-invi-gallary {\r\n        padding: 8px;\r\n    }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
