 item.imageOne\" alt=\"\" />\r\n                    </div>\r\n                    <div class=\"cere-view-info\">\r\n                        <h3>{{ titleCase(item.name) }}</h3>\r\n                        <div class=\"cere-view-info-time\">\r\n                            <div>\r\n                                <img\r\n                                    src=\"/assets/ceramonies/calendar.svg\"\r\n                                    alt=\"\"\r\n                                />\r\n                            </div>\r\n                            <div>\r\n                                <div>\r\n                                    {{\r\n                                        new Date(\r\n                                            item.startDate\r\n                                        ).toLocaleDateString(\"en-us\", {\r\n                                            weekday: \"long\",\r\n                                            year: \"numeric\",\r\n                                            month: \"long\",\r\n                                            day: \"numeric\",\r\n                                        })\r\n                                    }}\r\n                                </div>\r\n                                <div>\r\n                                    {{\r\n                                        new Date(\r\n                                            \"1970-01-01T\" + item.startTime + \"Z\"\r\n                                        ).toLocaleTimeString(\"en-US\", {\r\n                                            timeZone: \"UTC\",\r\n                                            hour12: true,\r\n                                            hour: \"numeric\",\r\n                                            minute: \"numeric\",\r\n                                        })\r\n                                    }}\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n                        <div class=\"cere-view-info-time\">\r\n                            <div>\r\n                                <img\r\n                                    src=\"/assets/ceramonies/map-pin.svg\"\r\n                                    alt=\"\"\r\n                                />\r\n                            </div>\r\n                            <div>\r\n                                <div v-for=\"e in item.venues\" :key=\"e.name\">\r\n                                    {{ e.name }}, {{ e.location.name }},\r\n                                    {{ e.location.state }}\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div\r\n                v-if=\"popupCera\"\r\n                class=\"cere-popup-container\"\r\n                @click=\"popupCera = null\"\r\n            >\r\n                <component\r\n                    @ceraClose=\"popupCera = null\"\r\n                    :ceremony=\"popupCera\"\r\n                    :brideName=\"invitation.brideName\"\r\n                    :groomName=\"invitation.groomName\"\r\n                    v-bind:is=\"ceremonyCard\"\r\n                ></component>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport CardDefault from \"./ceremonyCards/CardDefault.vue\";\r\nimport CardMehendi from \"./ceremonyCards/CardMehendi.vue\";\r\nimport CardRoka from \"./ceremonyCards/CardRoka.vue\";\r\nimport CardEngagement from \"./ceremonyCards/CardEngagement.vue\";\r\nimport CardSangeet from \"./ceremonyCards/CardSangeet.vue\";\r\nimport CardHaldi from \"./ceremonyCards/CardHaldi.vue\";\r\nimport CardReception from \"./ceremonyCards/CardReception.vue\";\r\nimport CardCustom from \"./ceremonyCards/CardCustom.vue\";\r\nimport CardTilak from \"./ceremonyCards/CardTilak.vue\";\r\nimport CardSagan from \"./ceremonyCards/CardSagan.vue\";\r\nimport CardWedding from \"./ceremonyCards/CardWedding.vue\";\r\n\r\nexport default {\r\n    components: {\r\n        CardDefault,\r\n        CardMehendi,\r\n        CardRoka,\r\n        CardEngagement,\r\n        CardSangeet,\r\n        CardHaldi,\r\n        CardReception,\r\n        CardCustom,\r\n        CardTilak,\r\n        CardSagan,\r\n        CardWedding,\r\n    },\r\n    props: [\"ceramonies\", \"invitation\"],\r\n    data() {\r\n        return {\r\n            popupCera: null,\r\n        };\r\n    },\r\n    methods: {\r\n        titleCase(str) {\r\n            let _this = this;\r\n            var splitStr = str.toLowerCase().split(\" \");\r\n            for (var i = 0; i < splitStr.length; i++) {\r\n                // You do not need to check if i is larger than splitStr length, as your for does that for you\r\n                // Assign it back to the array\r\n                splitStr[i] =\r\n                    splitStr[i].charAt(0).toUpperCase() +\r\n                    splitStr[i].substring(1);\r\n            }\r\n            // Directly return the joined string\r\n            return splitStr.join(\" \");\r\n        },\r\n    },\r\n    computed: {\r\n        ceremonyCard() {\r\n            return this.popupCera.file;\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.guest-invi-ceramony {\r\n    background-image: url(\"/assets/guestInvi/cera-back.png\");\r\n    background-repeat: no-repeat;\r\n    background-position: top left;\r\n    padding: 68px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n.guest-invi-ceramony > h3 {\r\n    font-family: \"Perpetua\";\r\n    font-style: normal;\r\n    font-weight: 700;\r\n    font-size: 29.5136px;\r\n    line-height: 34px;\r\n    color: #333333;\r\n    margin-bottom: 15px;\r\n}\r\n.guest-invi-ceramony > h2 {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 14.5337px;\r\n    line-height: 22px;\r\n    color: #000000;\r\n    margin-bottom: 15px;\r\n    text-align: center;\r\n}\r\n.cera-view-cont {\r\n    overflow-x: scroll;\r\n    -ms-overflow-style: none; /* IE and Edge */\r\n    scrollbar-width: none; /* Firefox */\r\n    display: flex;\r\n    width: 100%;\r\n    padding: 10px;\r\n}\r\n.cera-view-cont::-webkit-scrollbar {\r\n    display: none;\r\n}\r\n.cere-view-single {\r\n    background: #ffffff;\r\n    border: 0.575294px solid #9c9797;\r\n    border-radius: 8.05411px;\r\n    padding: 10px;\r\n    min-width: 230px;\r\n    margin-right: 15px;\r\n    max-width: 300px;\r\n    width: 100%;\r\n}\r\n.cere-single-image-cont {\r\n    position: relative;\r\n    padding-top: 80%;\r\n}\r\n.cere-single-image-cont > img {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    object-fit: cover;\r\n}\r\n.cere-single-image-cont > div {\r\n    position: absolute;\r\n    top: 15px;\r\n    right: 15px;\r\n    z-index: 99999;\r\n}\r\n.cere-view-info {\r\n    margin-top: 15px;\r\n}\r\n.cere-view-info > h3 {\r\n    font-family: \"Perpetua\";\r\n    font-style: normal;\r\n    font-weight: 700;\r\n    font-size: 18px;\r\n    line-height: 21px;\r\n    margin-bottom: 10px;\r\n    color: #461952;\r\n}\r\n.cere-view-info-time {\r\n    display: flex;\r\n    align-items: baseline;\r\n}\r\n.cere-view-info-time > div:nth-child(1) {\r\n    margin-right: 10px;\r\n}\r\n.cere-view-info-time > div:nth-child(2) {\r\n    font-family: \"Poppins\", sans-serif;\r\n    font-style: normal;\r\n    font-weight: 300;\r\n    font-size: 10.2px;\r\n    line-height: 15px;\r\n    color: #31373a;\r\n}\r\n.cere-popup-container {\r\n    background: rgba(38, 38, 38, 0.8);\r\n    position: fixed;\r\n    width: 100vw;\r\n    height: 100vh;\r\n    top: 0;\r\n    z-index: 999999;\r\n    display: flex;\r\n    justify-content: center;\r\n    overflow-y: auto;\r\n    -ms-overflow-style: none; /* IE and Edge */\r\n    scrollbar-width: none; /* Firefox */\r\n    padding: 10px;\r\n}\r\n.cere-popup-container::-webkit-scrollbar {\r\n    display: none;\r\n}\r\n@media screen and (max-width: 576px) {\r\n    .guest-invi-ceramony {\r\n        padding: 68px 21px;\r\n    }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
