Minutes = Math.floor(\r\n                    (totalRemainingMS % Onehour) / Oneminutes\r\n                );\r\n                let Seconds = Math.floor(\r\n                    (totalRemainingMS % Oneminutes) / 1000\r\n                );\r\n\r\n                const value = [Day, Hour, Minutes, Seconds];\r\n\r\n                function format(item) {\r\n                    if (item < 10) {\r\n                        return (item = `0${item}`);\r\n                    } else {\r\n                        return item;\r\n                    }\r\n                }\r\n                deadlineTimer.forEach(function (item, index) {\r\n                    item.innerHTML = format(value[index]);\r\n                });\r\n            }\r\n            let countdown = setInterval(getRemainingTime, 1000);\r\n            getRemainingTime();\r\n        },\r\n    },\r\n    mounted() {\r\n        this.$nextTick(function () {\r\n            this.countDown();\r\n        });\r\n        document.onreadystatechange = () => {\r\n            if (document.readyState == \"complete\") {\r\n                this.spliderEvent();\r\n            }\r\n        };\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.guest-story-cont {\r\n    padding-top: 40%;\r\n    position: relative;\r\n    width: 100%;\r\n    height: 100%;\r\n    top: 0;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n.guest-story-cont > img {\r\n    width: 100%;\r\n    height: 100%;\r\n    position: absolute;\r\n    top: 0;\r\n    object-fit: cover;\r\n}\r\n.edit-story {\r\n    position: absolute;\r\n    top: 20px;\r\n    right: 20px;\r\n    width: 40px !important;\r\n    height: 40px !important;\r\n    z-index: 99999;\r\n}\r\n.guest-story-cont > .overlay {\r\n    background: rgba(0, 0, 0, 0.5);\r\n    width: 100%;\r\n    height: 100%;\r\n    position: absolute;\r\n    top: 0;\r\n    z-index: 999;\r\n}\r\n.guest-story-main-img {\r\n    display: flex;\r\n    justify-content: center;\r\n   