oka-dress-cont > div {\r\n    display: flex;\r\n    justify-content: center;\r\n    width: 100%;\r\n}\r\n.cere-roka-dress-cont > div > div:nth-child(1) {\r\n    margin-right: 32px;\r\n}\r\n.dress-img {\r\n    width: 111px;\r\n    height: 73px;\r\n    margin-top: 8px;\r\n    border-radius: 2px;\r\n}\r\n.cere-roka-dress-cont {\r\n    margin-top: 13px;\r\n}\r\n\r\n.cere-roka-dress-cont > div {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 300;\r\n    font-size: 11.29px;\r\n    line-height: 17px;\r\n    color: #613f35;\r\n}\r\n.cere-reception-end {\r\n    font-family: \"Poppins\";\r\n    font-style: italic;\r\n    font-weight: 400;\r\n    font-size: 13px;\r\n    line-height: 20px;\r\n    text-align: center;\r\n    color: #613f35;\r\n    width: 80%;\r\n    margin-top: 15px;\r\n}\r\n@media screen and (max-width: 768px) {\r\n    .cera-single-popup-cont {\r\n        width: 95% !important;\r\n    }\r\n    .cere-roka-cont {\r\n        background-size: 40%, 40% !important;\r\n    }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
